import axios from "axios";
import { useEffect, useState } from "react";
import Page from "../components/Page";

const MenuPage = (props) => {

    const [menu, setMenu] = useState([]);

    useEffect(() => {
        axios
            .get(`${process.env.PUBLIC_URL}/assets/menu.json`)
            .then(reponse => {
                console.log(reponse.data);
                setMenu(reponse.data)
            });
    }, []);

    const createFood = (food) => {
        return <div className="food-container" key={food.title}>
            <div className="food-description">
                <div className="food-title">{food.title}</div>
                <div className="food-subtitle" dangerouslySetInnerHTML={{__html: food.subtitle}} />
                <div className="food-allergies">{JSON.stringify(food.allergies)}</div>
            </div>
            <div className="food-info">
                <div className="food-price">{food.price}</div>
                { food.special && <div className="food-special">{food.special}</div> }
            </div>
        </div>
    }

    return <Page id="menu" title="">
        {menu.map(category => {
            return <div key={category.title}>
                <div className="category-title">{category.title}</div>
                <div>
                    {category.foods.map(food => createFood(food))}
                </div>
            </div>
        })}
    </Page>
}

export default MenuPage;